      const that = this;
      if (
        typeof window.YT === "undefined" ||
        typeof window.YT.Player === "undefined"
      ) {
        const tag = document.createElement("script");
        tag.src = "https://www.youtube.com/iframe_api";
        const firstScriptTag = document.getElementsByTagName("script")[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
        window.onYouTubePlayerAPIReady = () => {
          that.onYouTubePlayer(index, videoId);
        };
      } else {
        that.onYouTubePlayer(index, videoId);
      }
    },
    ytStart(i) {
      document.getElementById(`player${i}`).src += "?autoplay=1";
      new window.YT.Player(`player${i}`, {
        videoId: "M7lc1UVf-VE",
        events: {
          // 'onReady': onPlayerReady,
          onStateChange: (event) => {
            console.log(event.data); // 0
          },
        },
      });
    },
    ytClose(i) {
      document.getElementById(`player${i}`).src = document
        .getElementById(`player${i}`)
        .src.slice(0, -11);
    },
    onYouTubePlayer(index, videoId) {
      console.log(index, videoId);
      setTimeout(() => {
        // 讓 Player 能抓到 v-if 內的 player id
        this.player[index] = new window.YT.Player(
          `player${this.currentTriggerModalIndex}`,
          {
            height: "470",
            width: "780",
            // videoId: this.youtubeVideoId,
            videoId: videoId,
            playerVars: {
              controls: 1,
              showinfo: 0,
              rel: 0,
              showsearch: 0,
              iv_load_policy: 3,
            },
            events: {
              onStateChange: this.onPlayerStateChange,
              onError: this.catchError,
            },
          }
        );
      }, 0);
    },
    onPlayerStateChange(event) {
      if (event.data === this.YOUTUBE_PLAYING) {
        this.startVideo();
      } else if (event.data === this.YOUTUBE_PAUSED) {
        this.pauseVideo();
      } else if (event.data === this.YOUTUBE_ENDED) {
        this.isPlay = false;
      }
    },
    startVideo() {
      this.player.playVideo();
      this.isPlay = true;
    },
    pauseVideo() {
      this.player.pauseVideo();
      this.isPlay = false;
    },
    catchError(err) {
      console.log(err);
    },
  },
};
</script>

