.popup {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 99;
  background: rgba(0, 0, 0, 0.8);
  display: none;
  &.show {
    display: block;
  }
  .content {
    width: 14.4rem;
    height: 7.5rem;
