    background-position: center center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    @media screen and (max-width: $sm) {
      width: 7.5rem;
      height: 100%;
      background: #fefbf6 url("../assets/img/kv/bg_inner.png") no-repeat;
      background-size: 7rem 93%;
      background-position: center center;
    }
    .bird {
      width: 3.77rem;
      height: 4.12rem;
      background: url("../assets/img/kv/bird.png");
      background-size: cover;
      position: absolute;
      transform: scale(0.6) rotate(-60deg);
      top: -0.9rem;
      right: 0rem;
      @media screen and (max-width: $sm) {
        display: none;
      }
    }
    .tree-flower {
      width: 1.01rem;
      height: 3.56rem;
      background: url("../assets/img/material/tree+flower1.png");
      background-size: cover;
      position: absolute;
      top: 3.8rem;
      left: 0;
      @media screen and (max-width: $sm) {
        display: none;
      }
    }
    .butterfly {
      width: 0.93rem;
      height: 0.96rem;
      background: url("../assets/img/kv/butterfly2.gif");
      background-size: cover;
      position: absolute;
      top: 5.5rem;
      right: 3rem;
      @media screen and (max-width: $sm) {
        top: unset;
        right: 3.2rem;
        bottom: 1.2rem;
      }
    }
    .island {
      width: 2.72rem;
      height: 1.79rem;
      background: url("../assets/img/kv/island.png");
